span {
  padding: 0.375rem 1rem;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  line-height: 1.5;
  font-size: 1rem;
  color: #390969;
  transition: 0.2s;
  transition-timing-function: ease;
  transition-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
  opacity: 0.8;
}
input:focus + span,
input:not(:placeholder-shown) + span {
  opacity: 1;
  transform: scale(0.75) translateY(-80%) translateX(-18px);
  background-color: rgba(236, 236, 236, 0.4);

  color: #390969;

  padding: 0.175rem 0.5rem;
}
textarea:focus + span,
textarea:not(:placeholder-shown) + span {
  opacity: 1;
  transform: scale(0.8) translateY(-80%) translateX(-1px);
  background-color: rgba(236, 236, 236, 0.4);

  color: #390969;
  padding: 0.175rem 0.5rem;
}

.placeholder {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  color: #858585;
}
.form__main {
  padding: 0 !important;
  border-color: #96b6e7;
  border: 100px;
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 14px;
  line-height: 21px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  color: black;
  background-color: rgba(236, 236, 236, 0.4);
  background-clip: padding-box;
  border: 1px solid #cadeef;
  border-radius: 0.5rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

